import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes, NavLink } from 'react-router-dom';
import EFPDash from './components/EFPDash';
import ListsChecker from './components/ListsChecker';
import Connection from './components/Connection';
import POAPs from './components/POAPs';
import ListDash from './components/ListDash';
import WordCloudPage from './components/WordCloud';

function App() {
  const [isMobile, setIsMobile] = useState(window.innerWidth < 600);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 600);
    };

    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <Router>
      <div className="App">
        <img src={`${process.env.PUBLIC_URL}/logo.png`} alt="Logo" className="logo" />
        <h1>EFPFinder</h1>
        <nav>
          <ul>
            {isMobile ? (
              <>
                <li>
                  <NavLink to="/efpdash" className={({ isActive }) => isActive ? 'active-link' : ''}>
                    <img src="/navi/EFPDash.png" alt="EFPDash" className="navibar" />
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/listdash" className={({ isActive }) => isActive ? 'active-link' : ''}>
                    <img src="/navi/ListDash.png" alt="ListDash" className="navibar" />
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/listschecker" className={({ isActive }) => isActive ? 'active-link' : ''}>
                    <img src="/navi/ListsHold.png" alt="ListsChecker" className="navibar" />
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/connection" className={({ isActive }) => isActive ? 'active-link' : ''}>
                    <img src="/navi/Connection.png" alt="Connection" className="navibar" />
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/poaps" className={({ isActive }) => isActive ? 'active-link' : ''}>
                    <img src="/navi/POAPs.png" alt="POAPs" className="navibar" />
                  </NavLink>
                </li>
              </>
            ) : (
              <>
                <li>
                  <NavLink to="/efpdash" className={({ isActive }) => isActive ? 'active-link' : ''}>EFPDash</NavLink>
                </li>
                <li>
                  <NavLink to="/listdash" className={({ isActive }) => isActive ? 'active-link' : ''}>ListDash</NavLink>
                </li>
                <li>
                  <NavLink to="/listschecker" className={({ isActive }) => isActive ? 'active-link' : ''}>ListsHold</NavLink>
                </li>
                <li>
                  <NavLink to="/connection" className={({ isActive }) => isActive ? 'active-link' : ''}>Connection</NavLink>
                </li>
                <li>
                  <NavLink to="/poaps" className={({ isActive }) => isActive ? 'active-link' : ''}>POAPs</NavLink>
                </li>
              </>
            )}
          </ul>
        </nav>
        <Routes>
          <Route path="/efpdash" element={<EFPDash />} />
          <Route path="/listdash" element={<ListDash />} />
          <Route path="/listschecker" element={<ListsChecker />} />
          <Route path="/connection" element={<Connection />} />
          <Route path="/poaps" element={<POAPs />} />
          <Route path="/wordcloud" element={<WordCloudPage />} />
          <Route path="/" element={<WordCloudPage />} />
        </Routes>   
        <footer className="footer">
          <p>© 2025 EFPFinder. Backed by <a href={`https://x.com/realjaxo`} target="_blank" rel="noopener noreferrer">jaxo</a></p> 
        </footer>
      </div>
    </Router>
  );
}

export default App; 